import { GraphQLClient } from 'graphql-request';

const API_URL = process.env.NEXT_PUBLIC_API_URL!;

export const graphql = new GraphQLClient(API_URL);

// Create Application
export const CREATE_APPLICATION = `
  mutation CreateApplication($input: CreateApplicationInput!) {
    createApplication(input: $input) {
      id
      name
      urlDocBase
      description
    }
  }
`;

// List Applications
export const GET_APPLICATIONS = `
  query GetApplications {
    applications {
      id
      name
      urlDocBase
      description
      status
      createdAt
    }
  }
`;

// Start Crawl Job
export const START_CRAWL = `
  mutation StartCrawl($input: StartCrawlInput!) {
    startCrawl(input: $input) {
      jobId
      status
      message
    }
  }
`;

// Get Crawl Jobs  
export const GET_CRAWL_JOBS = `
  query GetCrawlJobs($appId: ID, $limit: Int) {
    crawlJobs(appId: $appId, limit: $limit) {
      id
      appId
      status
      startTime
      endTime
      pagesProcessed
      errors {
        id
        errorMessage
        url
      }
    }
  }
`;

export interface Application {
  id: string;
  name: string;
  urlDocBase: string;
  description?: string;
  status?: string;
  createdAt?: string;
}

export interface CrawlJob {
  id: string;
  appId: string;
  status: string;
  startTime: string;
  endTime?: string;
  pagesProcessed?: number;
  errors?: Array<{
    id: string;
    errorMessage: string;
    url: string;
  }>;
}

export interface CreateUserInput {
  email: string;
  password: string;
  fullName?: string;
}

export const CREATE_USER = `
    mutation CreateUser($input: CreateUserInput!) {
        createUser(input: $input) {
            id
            email
            createdAt
        }
    }
`;

export interface User {
  id: string;
  email: string;
  createdAt: string;
  lastSignInAt?: string;
}

export const GET_USERS = `
    query GetUsers {
        users {
            id
            email
            createdAt
            lastSignInAt
        }
    }
`;
